<template>
    <v-select
            v-model="val"
            :items="items"
            label=""
            :rules="rules"
            @change="$emit('change',$event)"
    ></v-select>
</template>

<script>
    export default {
        name: "ArtifactSubTypeSelect",
        data:()=>({
            val:'PAINTING',
            items:[
                {value:'PAINTING',text: ''},
                {value: 'PORCELAIN',text: ''},
                {value: 'MISC',text: ''}
            ],
            rules:[v=>v !== undefined || '']
        }),
        mounted() {
            this.$emit('change','PAINTING')
        }
    }
</script>

