<template>
    <v-select
            v-model="val"
            :items="items"
            label=""
            :rules="rules"
            @change="$emit('change',$event)"
    ></v-select>
</template>

<script>
    export default {
        name: "TypeSelect",
        data:()=>({
            val:'NEWS',
            items:[
                {value:'NEWS',text: ''},
                {value: 'ACTIVITY',text: ''},
                {value: 'POLICY',text: ''},
                {value: 'ANNOUNCE',text: ''},
                {value: 'TAX_FREE',text: ''},
                {value: 'EXHIBITION',text: ''},
                {value: 'AUCTION',text: ''},
                {value:'EXHIBITION_INFO',text: ''}
                ],
            rules:[v=>v !== undefined || '']
        }),
        created() {
            if(this.$store.state.userObj.type !== 'ADMIN'){
                const arr = this.items.filter(function (word) {
                     return word.value === 'EXHIBITION' || word.value === 'AUCTION'
                })
                this.items.length = 0
                this.items.push(...arr)
                this.val = 'EXHIBITION'
            }
        }
    }
</script>

