<template>
    <v-select
            v-model="val"
            :items="items"
            label=""
            :rules="rules"
            @change="$emit('change',$event)"
    ></v-select>
</template>

<script>
    export default {
        name: "AuctionSubTypeSelect",
        data:()=>({
            val:'SPRING_AUTUMN',
            items:[
                {value:'SPRING_AUTUMN',text: ''},
                {value: 'WANG_SU',text: ''},
                {value: 'ONLINE_AUCTION',text: ''}
            ],
            rules:[v=>v !== undefined || '']
        }),
        mounted() {
            this.$emit('change','SPRING_AUTUMN')
        }
    }
</script>

